@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Varela Round', sans-serif;
    background-color:  rgb(2, 126, 241);
}
#heading{
    color: white;
    text-align: center;
    font-size: 2.3rem;
    margin-top: 30px;
}

#quote-box{
    background-color: aliceblue;
    width: 40rem;
    margin: 8rem auto;
    border-radius: 5px;
    padding: 3rem;
}

#text{
    font-size: 2rem;
    margin-bottom: 1.5rem;
}

#author{
    display: flex;
    justify-content: end;
    font-size: 1.3rem;
}

#new-quote-btn{
    display: flex;
    justify-content: center;
}

#new-quote{
    background-color: rgb(2, 126, 241);
    color: white;
    font-size: 1.1rem;
    width: 130px;
    height: 40px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

#new-quote:hover{
    background-color: rgba(2, 125, 241, 0.815);
}

#tweet-quote:hover{
    opacity: 0.8;
}
#tumblr-quote:hover{
    opacity: 0.8;
}
@media only screen and (max-width: 600px) {
    #quote-box{
        width: 100%;
        border-radius: 3px;
        padding: 1rem;
    }
    #heading{
        font-size: 1.1rem;
    }
    #text{
        font-size: 1.1rem;
        justify-content: center;
        font-weight: bold;
        padding-top: 30px;
    }

    #author{
        display: flex;
        justify-content: end;
        font-size: 1rem;
        position: relative;
    }

    #new-quote{
       
        font-size: 1rem;
        width: 100px;
        height: 30px; 
        border-radius: 5px;
    }


}